#home_container {
      width: 100vw;
      height: 100vh;
      display: flex;
      text-align: right;
      justify-content: flex-end;
      align-content: right;
      align-items: center;
}


h2 {
      text-shadow:#b08338 0.042em -0.042em;
}

h6 {
      color: #ffc159;
}


.header_title {
      color: #5ee2c3;
      font-weight: 300;
      font-stretch: normal;
      font-size: 16px;
      line-height: 1.5;
      font-weight: 300;
      padding: 0 10% 20% 10%;
      height: auto;
      margin: 16px;
}

#home_background_animation {
      position: absolute;
      width: 100vw;
      height: 100vh;
      z-index: -1;
}
